.cart-item {

  .card-block {
    display: flex;
    max-width: 650px;
    box-shadow: 17px 19px 24px rgba(0, 0, 0, 0.13);
    margin-bottom: 40px;
    max-height: 306px;

    &__img img {
      height: 100%;
      object-fit: contain; }
    &__descr {
      flex: 1;
      padding: 20px 20px 20px 30px;
      background-color: #fff; }

    &__title {
      font-weight: 400;
      font-size: 24px;
      color: #222222;
      margin-bottom: 40px;

      &--wrap {
        display: flex;
        justify-content: space-between;
        align-items: start; } }

    &__close-btn {
      margin-top: 6px; }

    &__detail {
      font-weight: 400;
      font-size: 22px;
      line-height: 26px;
      color: #575757;
      & > div {
        margin-bottom: 6px; }
      &--price span {
        color: #EF5B70; }

      &--quantity span {
        border: 1px solid #EAEAEA;
        padding: 0 14px;
        margin-left: 24px; } } } }






