.block-collection {
  position: relative;
  background-color: #fff;
  top: -64px;
  margin-bottom: -64px;
  border: 1px solid #EAEAEA;
  padding-top: 64px;
  padding-left: 16px;
  padding-right: 16px;
  @include md {
    padding-left: 0;
    padding-right: 0; }

  &__title {
    text-align: center;
    &--main {
      font-weight: 300;
      font-size: 14px;
      color: #F16D7F;
      margin-bottom: 12px;
      text-transform: uppercase; }
    &--redline {
      position: relative;
      padding-bottom: 12px;
      &:after {
        position: absolute;
        content: '';
        left: 50%;
        transform: translateX(-50%);
        width: 63px;
        height: 3px;
        background-color: #EF5B70; } }
    &--subtitle {
      font-weight: 300;
      font-size: 18px;
      color: #4D4D4D;
      margin-bottom: 48px; } }


  &__paragraph {
    text-align: center;
    max-width: 555px;
    margin: 0 auto;
    &--text {
      font-weight: 300;
      font-size: 14px;
      color: #5E5E5E;
      margin-bottom: 2rem; } }


  &__price {
    text-align: center;
    margin-bottom: 64px;
    &--text {
      font-weight: 300;
      font-size: 24px;
      color: #EF5B70; } }


  &__line {
    position: relative;
    padding-bottom: 64px;
    &:after {
      position: absolute;
      content: '';
      left: 50%;
      transform: translateX(-50%);
      width: 174px;
      height: 1px;
      background-color: #EAEAEA;
      @include md {
        width: 642px; } } }


  &__filter {
    display: flex;
    justify-content: center;
    position: relative;
    margin-bottom: 64px;
    @include md {
      margin-bottom: 40px;
      justify-content: center; }
    @include lg {
      margin-bottom: 48px;
      justify-content: center; }
    &-wrap {
      display: flex;
      width: 100%;
      justify-content: space-around;
      @include md {
        width: auto; }
      &-title {
        display: flex;
        align-items: center;
        font-weight: 400;
        font-size: 10px;
        line-height: 17px;
        color: #6F6E6E;
        margin: 0;
        @include md {
          font-size: 14px;
          margin: 0 14px; }
        & a {
          display: flex;
          justify-content: center;
          margin-left: 10px; } } } }


  &__btn {
    display: flex;
    justify-content: center;
    margin-bottom: 64px;
    & .btn-add-cart {
        display: inline-block;
        position: relative;
        padding: 0 39px 0 59px;
        line-height: 48px;
        border: 1px solid #FF6A6A;
        font-weight: 400;
        font-size: 16px;
        color: #F26376;
        text-decoration: none;
        text-align: center;
        &:before {
          position: absolute;
          content: '';
          background-image: url('../img/cart-red.svg');
          width: 27px;
          height: 25px;
          left: 25px;
          top: 50%;
          transform: translateY(-50%);
          background-repeat: no-repeat;
          background-position: center center; }
        &:hover {
            background: #F16D7F;
            border: 1px solid #FF6A6A;
            color: #fff;
            &:before {
              background-image: url('../img/cart-white.svg'); } } } } }








